@charset "UTF-8";

@import url(../../node_modules/normalize.css/normalize.css);

html {
  position: relative;
  min-height: 100%;
  font-size: 16px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Ubuntu", Helvetica, Arial, sans-serif;
  margin: 0;
  padding: 0;
  width: 100%;
  min-height: 100%;
}

img,
embed,
object,
video {
  max-width: 100%;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
}

.wrapper {
  position: relative;
  min-height: 100%;
  overflow: hidden;
}

.container {
  position: relative;
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
}

.section {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 4.6875rem 0;
}

.title {
  font-family: "BebasNeue", Helvetica, Arial, sans-serif;
  color: #414141;
  font-size: 2.75rem;
  margin-bottom: 4.6875rem;
  line-height: 1;
}

.title_white {
  color: #ffffff;
}

.title_center {
  text-align: center;
}

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/Ubuntu-Light.woff2") format("woff2");
}

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Ubuntu-Regular.woff2") format("woff2");
}

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Ubuntu-Medium.woff2") format("woff2");
}

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Ubuntu-Bold.woff2") format("woff2");
}

@font-face {
  font-family: "BebasNeue";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/BebasNeue-Bold.woff2") format("woff2");
}

@font-face {
  font-family: "FA";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/fa-solid-900.woff2") format("woff2");
}

.about {
  background-color: #ededed;
  padding-top: 1.5625rem;
}

.button {
  background-color: #711f1f;
  color: #ffffff;
  border-radius: 4px;
  text-decoration: none;
  padding: 0.9375rem 1.25rem;
}

.calc {
  display: flex;
  background-color: rgba(0, 0, 0, 0.4);
  width: 529px;
}

.faq {
  background-color: #ededed;
  position: relative;
}

.faq::after {
  content: '?';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(0, -50%);
  font-family: "BebasNeue", Helvetica, Arial, sans-serif;
  font-size: 60rem;
  color: #414141;
  opacity: .06;
}

.faq__list {
  display: flex;
  flex-direction: column;
}

.faq__item {
  color: #414141;
  margin-bottom: 1.25rem;
}

.faq__title {
  font-family: "BebasNeue", Helvetica, Arial, sans-serif;
  font-size: 1.3125rem;
  line-height: 1.5;
  color: #711f1f;
}

.faq__text {
  line-height: 1.6;
  font-weight: 300;
  color: #414141;
}

.features {
  display: flex;
  margin-top: 5rem;
}

.features__list {
  display: flex;
  justify-content: space-around;
}

.features__item {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.features__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: #ffffff;
  margin-bottom: 1rem;
  box-shadow: -10px -10px 30px 4px rgba(113, 31, 31, 0.1), 10px 10px 30px 4px rgba(113, 31, 31, 0.15);
}

.features__num {
  font-weight: 700;
  color: #711f1f;
  font-size: 2rem;
}

.header {
  display: flex;
  background-color: #ffffff;
  padding: 1rem;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hero {
  background: url(../img/bg-hero.jpg) center/cover no-repeat;
  height: 100vh;
  padding: 0;
}

.hero__container {
  display: flex;
  height: 100%;
}

.hero__wrap {
  position: relative;
  height: 100vh;
}

.hero__wrap::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, rgba(0, 0, 0, 0.85) 10%, rgba(0, 0, 0, 0) 100%);
}

.hero__content {
  display: flex;
  align-items: center;
  height: 100%;
}

.hero__left {
  flex-basis: 50%;
}

.hero__right {
  flex-basis: 50%;
}

.hero__title {
  display: flex;
  font-size: 4.0625rem;
  border-left: 10px solid #711f1f;
  padding-left: 0.625rem;
}

.loan {
  background: url(../img/bg-loan.jpg) center/cover no-repeat;
  box-shadow: inset 0 0 100px black;
}

.loan__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.loan__title {
  margin-bottom: 2rem;
}

.loan__subtitle {
  font-size: 2rem;
  margin-bottom: 2rem;
}

.logo {
  font-family: "BebasNeue", Helvetica, Arial, sans-serif;
  color: #414141;
  font-size: 2.25rem;
}

.logo span {
  color: #711f1f;
}

.menu {
  display: flex;
}

.menu__list {
  display: flex;
}

.menu__item {
  margin-right: 1.5625rem;
}

.menu__link {
  font-family: "BebasNeue", Helvetica, Arial, sans-serif;
  color: #414141;
  font-size: 1.125rem;
}

.payments__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.payments__title {
  margin-bottom: 2rem;
}

.payments__list {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
}

.payments__item {
  width: 80px;
  margin: 0 1rem;
}

.payments__img {
  max-width: 100%;
}

.phone {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.phone__number {
  font-family: "BebasNeue", Helvetica, Arial, sans-serif;
  color: #414141;
  font-size: 2.25rem;
}

.phone__callback {
  color: #414141;
  font-weight: 300;
  text-decoration: underline;
}

.questions {
  width: 100%;
  border-radius: 0.375rem;
  padding: 0 1rem;
  margin-bottom: 4rem;
}

.questions__content {
  display: flex;
  border-bottom: 0 none;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.46);
}

.questions__icon {
  display: flex;
  align-items: center;
  color: #711f1f;
  font-size: 3.75rem;
  margin-right: 1rem;
  background-color: #ffffff;
  padding: 1rem 2rem;
}

.questions__icon::before {
  content: '';
  font-family: 'FA';
}

.questions__text {
  padding: 1rem;
  font-size: 1.125rem;
}

.questions__title {
  font-family: "BebasNeue", Helvetica, Arial, sans-serif;
  font-size: 2.625rem;
  color: #414141;
  margin-bottom: .625rem;
}

.topline {
  background-color: #414141;
}

.topline__container {
  display: flex;
  justify-content: flex-end;
}

.topline__list {
  display: flex;
  padding: 1rem 0;
}

.topline__item {
  display: flex;
  margin-right: 1rem;
}

.topline__item:last-child {
  margin-right: 0;
}

.topline__link {
  color: #ffffff;
  font-size: 0.8125rem;
  font-weight: 300;
}

.topline__link::before {
  content: '';
  font-family: 'FA';
  margin-right: 0.625rem;
}

.topline__link_city::before {
  content: '';
}

.topline__link_address::before {
  content: '';
}

.topline__link_user::before {
  content: '';
}

.work__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.work__item {
  display: flex;
  align-items: flex-end;
  flex: 1 0 50%;
  margin-bottom: 6rem;
  position: relative;
}

.work__item:nth-last-child(-n+2) {
  margin-bottom: 0;
}

.work__item::before {
  content: '';
  font-family: "BebasNeue", Helvetica, Arial, sans-serif;
  color: #414141;
  font-size: 15rem;
  position: absolute;
  left: -3rem;
  bottom: -4rem;
  opacity: .08;
  z-index: -10;
}

.work__item:nth-child(1)::before {
  content: '1';
}

.work__item:nth-child(2)::before {
  content: '3';
}

.work__item:nth-child(3)::before {
  content: '2';
}

.work__item:nth-child(4)::before {
  content: '4';
}

.work__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'FA';
  color: #ffffff;
  font-size: 2rem;
  min-width: 5rem;
  min-height: 5rem;
  background-color: #711f1f;
  border-radius: 50%;
}

.work__content {
  display: flex;
  flex-direction: column;
  align-self: baseline;
  padding: 0 2rem;
}

.work__title {
  font-family: "BebasNeue", Helvetica, Arial, sans-serif;
  font-size: 1.75rem;
  color: #414141;
}

.work__subtitle {
  font-size: .875rem;
  font-weight: 300;
  margin-bottom: 1rem;
}

.work__content-list {
  font-size: .875rem;
  font-weight: 300;
}

.work__content-item {
  display: flex;
  align-items: flex-start;
  margin-bottom: .625rem;
}

.work__content-item::before {
  content: '';
  font-family: 'FA';
  font-size: 1rem;
  color: #711f1f;
  margin-right: 0.3125rem;
}

@media (max-width: 768px) {
  html {
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  html {
    font-size: 13px;
  }
}